.bubbles {
  margin: 100px auto 250px;
  padding: 100px 0;
  height: 340px;

  &__row {
    margin-bottom: 70px;
    @include flex-v-center;
  }

  &__img {
    margin-right: 45px;

    @include tablet800 {
      display: none;
    }
  }

  &__text {
    font-size: 30px;
    line-height: 35px;
    letter-spacing: 0.045em;

    @include mobile {
      font-size: 20px;
      text-align: center;
    }
  }

  @include tablet800 {
    margin: 50px auto 100px;
    padding: 0;
  }
}