.contacts {
  &__title {
    margin: 75px 0 115px;
  }

  &__row {
    display: flex;
    align-items: center;
    
    margin-bottom: 52px;
  }

  &__icon {
    margin-right: 45px;
  }

  &__text {
    font-size: 18px;
    line-height: 21px;
    letter-spacing: 0.045em;
    color: var(--dark-color);
  }

  &__geo {
    display: flex;
    align-items: center;
    margin: 130px 0 220px;
  }

  &__address {
    margin-left: 8px;
    font-size: 18px;
    line-height: 21px;
    letter-spacing: 0.045em;
    color: var(--dark-color);
  }
}