.intro {
  margin: 100px auto 300px;
  padding: 100px 0;
  height: 415px;

  &__inner {
    display: flex;
  }

  &__left {
    display: flex;
    flex-direction: column;
    width: 55%;

    @include tablet955 {
      width: 100%;
      align-items: center;
    }

    @include tablet768 {
      padding: 0 20px;
    }
  }

  &__title {
    margin-bottom: 100px;

    @include mobile {
      margin-bottom: 50px;
    }
  }

  &__text {
    margin-bottom: 100px;
    max-width: 476px;
    line-height: 29px;
    letter-spacing: 0.045em;

    @include mobile {
      // margin-bottom: 30px;
      font-size: 20px;
    }
  }

  &__btn {
    max-width: 300px;
    padding: 20px 44px;
  }

  &__right {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 45%;

    @include tablet955 {
      display: none;
    }
  }

  &__circle {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -10;
  }

  &__chocolate {
    position: absolute;
    top: 170px;
    left: 0;
    z-index: -9;
  }

  &__bouquet {
    position: absolute;
    top: -30px;
    right: 30px;
    z-index: -8;
  }

  &__hares {
    position: absolute;
    bottom: -80px;
    right: 40px;
    z-index: -7;
  }

  @include mobile {
    margin-bottom: 200px;
  }
}