.btn {
  font-weight: 500;
  font-size: 20px;
  line-height: 23px;
  letter-spacing: 0.045em;

  color: var(--dark-color);
  background-color: var(--green-color);
  box-shadow: var(--shadow-btn-color);
  border-radius: 15px;

  transition: filter .1s ease-in-out;

  &:hover {
    filter: brightness(85%);
  }
}