.card {
  position: relative;
  margin: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 21px;
  width: 330px;
  height: 434px;

  background: var(--bg-card-color);
  box-shadow: var(--shadow-card-color);
  border-radius: 15px;

  &__bouquet {
    margin: 0;
    width: 350px;
    height: 434px;
  }

  &__title {
    font-weight: 500;
    font-size: 25px;
    line-height: 29px;
    letter-spacing: 0.045em;
  }

  &__img {
    border-radius: 15px;
  }

  &__price {
    line-height: 30px;
    letter-spacing: 0.045em;
  }

  &__btn {
    padding: 15px 45px;
    max-width: 185px;
  }

  @include tablet821min {
    width: 100%;
  }

  @include mobile {
    margin: 10px 0;
  }
}

.swiper-pagination-bullet {
  width: 17px;
  height: 17px;
  line-height: 20px;
  font-size: 14px;
  background-color: #000;
  transform: translateY(80px);

  &:not(:last-child) {
    margin-left: 25px;
  }
}

