// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Raleway", sans-serif;
  --content-width: 1065px;
  --content-fluid-width: 1450px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --container-fluid-width: calc(var(--content-fluid-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --green-color: #daffce;
  --gray-color: #7c7c7c;
  --grayest-color: #484848;
  --dark-color: #000;
  --bg-color: #f5f5f5;
  --bg-card-color: #fcfff9;
  --shadow-btn-color:  0px 4px 4px rgba(0, 0, 0, 0.25);
  --shadow-card-color: 0px 4px 4px 4px rgba(0, 0, 0, 0.07);;
}
