.nav {
  &__list {
    @include flex-all-sb;
    width: 900px;

    @include tablet992 {
      @include flex-all-center;
      flex-direction: column;

      width: 100%;
      height: 100%;
    }
  }

  &__link {
    font-size: 23px;
    line-height: 26px;
    letter-spacing: 0.045em;
    color: var(--dark-color);
  }

  &__item {
    @include tablet992 {
      margin-bottom: 50px;
    }
  }

  @include tablet992 {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 500;
    overflow-y: auto;
    width: 100%;
    height: 100vh;
    background-color: var(--bg-color);
    opacity: 0;
    visibility: hidden;
    transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
  }
}


.menu--active {
  opacity: 1;
  visibility: visible;
}