.header {
  position: relative;

  &__inner {
    @include flex-all-sb;
    padding: 35px 0;
  }

  &::before {
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    display: block;
    width: 100%;
    height: 1px;
    background-color: var(--dark-color);
  }

  &::after {
    position: absolute;
    left: 0;
    bottom: 0;
    content: "";
    display: block;
    width: 100%;
    height: 1px;
    background-color: var(--dark-color);
  }
}