.products {
  margin: 100px auto;
  margin-bottom: 350px;
  padding: 100px 0;
  height: 500px;

  &__container-fluid {
    position: relative;
  }

  &__title {
    margin-bottom: 60px;
  }

  &__img {
    position: absolute;
  
    &-prew {
      top: 55%;
      left: 10%;
      z-index: 5;

      @include desktop1380 {
        left: 5%;
      }

      @include desktop1276 {
        left: 1%;
      }

    }

    &-next {
      top: 55%;
      right: 10%;
      z-index: 5;

      @include desktop1380 {
        right: 5%;
      }
      
      @include desktop1276 {
        right: 1%;
      }

    }

    @include mobile {
      display: none;
    }
  }
}