.footer {
  height: 170px;

  &__inner {
    @include flex-sb;

    @include mobile {
      flex-direction: column;
      align-items: center;
    }
  }

  &__title {
    margin-bottom: 33px;
    font-weight: 500;
    font-size: 20px;
    line-height: 23px;
    letter-spacing: 0.045em;
  }

  &__links {
    @include flex-sb;

    @include mobile {
      margin-bottom: 25px;
    }
  }

  &__infos {
    @include flex-sb;
    flex-direction: column;

    font-weight: 500;
    font-size: 20px;
    line-height: 23px;
    letter-spacing: 0.045em;

    @include mobile {
      margin-bottom: 20px;
    }
  }
}