.form {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__label {
    margin-bottom: 50px;
    max-width: 536px;
    width: 100%;
  }

  &__input {
    margin-bottom: 26px;
    padding: 15px 20px;
    width: 100%;
    height: 59px;

    font-weight: 500;
    font-size: 25px;
    line-height: 29px;
    letter-spacing: 0.045em;

    background-color: inherit;

    border: 1px solid #000000;
    border-radius: 15px;
  }

  &__btn {
    padding: 20px 65px;
  }
}