.order {
  &__title {
    margin: 75px 0 45px;
  }

  &__text {
    margin-bottom: 70px;
    max-width: 735px;
    line-height: 29px;
    letter-spacing: 0.045em;

    &:last-child {
      margin: 75px 0;
    }
  }

  &__point {
    margin-bottom: 10px;
    font-size: 20px;
    line-height: 23px;
    letter-spacing: 0.045em;

    &:last-child {
      margin-bottom: 100px;
    }
  }

  &__box {
    max-width: 180px;
    text-align: center;
  }

  &__arrow {
    margin-bottom: 10px;
  }

  &__link {
    text-decoration: none;
    color: var(--dark-color);
  }
}