.burger {
  position: relative;
  border: 2px solid var(--dark-color);
  border-radius: 10px;
  width: 44px;
  height: 44px;

  &__line {
    position: absolute;
    top: 50%;
    left: 50%;
    border-radius: 100px;
    height: 3px;
    width: 30px;
    background-color: var(--dark-color);
    transform: translate(-50%, -50%);

    &:first-child {
      margin-top: -0.4rem;
    }

    &:last-child {
      margin-top: 0.4rem;
    }
  }

  @include tablet992min {
    display: none;
  }
}

.burger-close {
  position: absolute;
  right: 3rem;
  top: 3rem;
  border: 2px solid var(--dark-color);
  border-radius: 10px;
  width: 44px;
  height: 44px;
  background-position: center;
  background-size: 24px 24px;
  background-repeat: no-repeat;

  @include tablet992min {
    display: none;
  }
}